.post-div {
  background-color: #18191a;
  padding: 0.5rem 0;
  color: #eee;
}

.post {
  background-color: #242526;
  margin: 0.5rem 0;
  border-radius: 0.5rem;
}

.post-user {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.post-user-info {
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.post-user-avatar {
  font-size: 2rem;
  padding: 0.5rem;
  margin-right: 0.5rem;

  &:hover {
    cursor: pointer;
  }
}

.post-user-opt {
  font-size: 1.2rem;
  padding: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: center;

  &:hover {
    cursor: pointer;
    background-color: #494949;
    border-radius: 50%;
    line-height: initial;
  }
}

.post-user-name {
  font-weight: bold;

  &:hover {
    cursor: pointer;
    text-decoration: underline;
  }
}

.post-user-date {
  font-size: 0.9rem;

  &:hover {
    cursor: pointer;
    text-decoration: underline;
  }
}

.post-likes {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.5rem;
}

.post-comment {
  display: flex;
  padding: 0.5rem;
  align-items: center;
  justify-content: space-between;
}

.post-buttons {
  display: flex;
}

.post-buttons-btn {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.5rem;

  span {
    font-size: 0.9rem;
  }

  &:hover {
    background-color: #3a3b3c;
    border-radius: 1rem;
    cursor: pointer;
  }
}

.post-comment-comment {
  background-color: #3a3b3c;
  width: 100%;
  padding: 0.5rem;
  border-radius: 1rem;

  input {
    width: 100%;
    height: 100%;
    border: none;
    color: #eee;
    background-color: transparent;
    margin-left: 0.5rem;

    &::placeholder {
      color: #a7aaaf;
      font-size: 1rem;
    }
  }
}

.post-comment-avatar {
  font-size: 1.1rem;
  margin-right: 0.5rem;

  &:hover {
    cursor: pointer;
  }
}

.post-likes-emojis {
  min-width: 50px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;

  &:hover {
    cursor: pointer;
  }
}

.post-likes-comments {
  span {
    &:hover {
      cursor: pointer;
      text-decoration: underline;
    }
  }
}

.post-img {
  img {
    width: 100%;
    height: auto;
    max-height: 350px;
  }
}

.post-avatar-name {
  display: flex;
}

/* MQ */

@media all and (min-width: 1024px) {
  .post-div {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .post {
    padding: 1rem;
    max-width: 600px;
  }

  .post-img {
    img {
      width: 100%;
      height: auto;
      max-height: 350px;
    }
  }

  .post-buttons-btn {
    span {
      font-size: 1rem;
    }

    .far,
    .fas {
      margin-right: 0.5rem;
    }
  }
}
@media all and (min-width: 768px) {
  .post-div {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .post {
    padding: 1rem;
    max-width: 600px;
  }

  .post-img {
    img {
      width: 100%;
      height: auto;
      max-height: 350px;
    }
  }
}
@media all and (min-width: 480px) {
  .post-div {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .post {
    padding: 1rem;
    max-width: 600px;
  }

  .post-img {
    img {
      width: 100%;
      height: auto;
      max-height: 350px;
    }
  }
}
